import * as React from 'react';
import { ColumnsPlugin, PluginRoles } from '@ali/4ever-bamboo';
import { MenuItem } from '@ali/we-design';
import { MenuItemInnerProps } from '@ali/4ever-component';

const { insertColumns } = ColumnsPlugin.actions;

export default function getMenu(size: number, configs: any) {
  return function ColumnsMenu(props: MenuItemInnerProps) {
    const { menuItem, controller } = props;
    const { title, ...rest } = menuItem;

    const { isCollapsedAtEdge } = controller.value.selection;

    const handleClick = () => {
      controller.run('onAction', insertColumns({ size, text: '' }));
    };

    return (
      <MenuItem {...rest} disabled={isCollapsedAtEdge} onClick={handleClick} role={`${PluginRoles.columns}${size}`}>
        {title || ''}
      </MenuItem>
    );
  };
}
